import React, { useState } from "react";
// Material UI
import { Tabs, Tab, Divider, Grow, Paper, Typography } from "@material-ui/core";
import {
  Timeline,
  TimelineConnector,
  TimelineContent,
  TimelineDot,
  TimelineItem,
  TimelineOppositeContent,
  TimelineSeparator,
} from "@material-ui/lab";
import AssignmentTurnedInIcon from "@material-ui/icons/AssignmentTurnedIn";
// components
import Y2015 from "./tabs/2015";
import Y2016 from "./tabs/2016";
import Y2017 from "./tabs/2017";
import Y2018 from "./tabs/2018";
import Y2019 from "./tabs/2019";
// import ItemTwo from "./tabs/ItemTwo";
// import ItemThree from "./tabs/ItemThree";
// Images
import logo from "../../asset/logo.png";

const Work = () => {
  const styles = {
    timeLineDotGreen: { color: "#fff", backgroundColor: "green" },
  };

  const timeLineItems = [
    {
      year: 2015,
      component: <Y2015 />,
    },
    {
      year: 2016,
      component: <Y2016 />,
    },
    {
      year: 2017,
      component: <Y2017 />,
    },
    {
      year: 2018,
      component: <Y2018 />,
    },
    {
      year: 2019,
      component: <Y2019 />,
    },
  ];

  return (
    <Grow in={true} style={{ transformOrigin: "0 0 0" }} timeout={2000}>
      <Paper
        style={{
          display: "flex",
          alignItems: "center",
          flexDirection: "column",
          margin: "-60px 30px 0px",
          boxShadow:
            "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
        }}
      >
        <img
          alt="Profile Pic"
          src={logo}
          style={{
            maxWidth: "250px",
            borderRadius: "50%",
            transform: "translate3d(0, -50%, 0)",
          }}
        />
        <Timeline align="alternate" style={{ marginTop: "-100px" }}>
          {timeLineItems.map((timeLineItem, index) => {
            return (
              <TimelineItem>
                <TimelineOppositeContent>
                  <Typography variant="body2" color="textSecondary">
                    {timeLineItem.year}
                  </Typography>
                </TimelineOppositeContent>
                <TimelineSeparator>
                  <TimelineDot style={styles.timeLineDotGreen}>
                    <AssignmentTurnedInIcon />
                  </TimelineDot>
                  <TimelineConnector />
                </TimelineSeparator>
                <TimelineContent style={{ textAlign: "left" }}>
                  {timeLineItem.component}
                </TimelineContent>
              </TimelineItem>
            );
          })}
        </Timeline>
      </Paper>
    </Grow>
  );
};

export default Work;
