import React from "react";
import {
  Typography,
  Accordion,
  AccordionDetails,
  AccordionSummary,
} from "@material-ui/core";
import { ExpandMore } from "@material-ui/icons";
// Styles
import { useStyles } from "./ItemStyles";

const Y2016 = () => {
  const classes = useStyles();
  return (
    <Accordion>
      <AccordionSummary
        expandIcon={<ExpandMore />}
        aria-controls="panel1bh-content"
        id="panel1bh-header"
      >
        <Typography className={classes.heading}>
          AutoParts Web Service
        </Typography>
      </AccordionSummary>
      <AccordionDetails>
        <ul>
          <li>
            <Typography>
              Implemented Java code from project design artifacts. (UML)
            </Typography>
          </li>
          <li>
            <Typography>
              Helped design and developed Auto Parts Locator web service using
              Spring Boot REST.
            </Typography>
          </li>
          <li>
            <Typography>
              Helped design and develop AutoParts Category using java Object
              Pool Design Pattern
            </Typography>
          </li>
          <li>
            <Typography>
              Developed Unit tests using jUnit framework and code coverage using
              JaCoCo
            </Typography>
          </li>
        </ul>
      </AccordionDetails>
    </Accordion>
  );
};

export default Y2016;
