import React from "react";
import {
  Typography,
  Accordion,
  AccordionDetails,
  AccordionSummary,
} from "@material-ui/core";
import { ExpandMore } from "@material-ui/icons";
// Styles
import { useStyles } from "./ItemStyles";

const Y2019 = () => {
  const classes = useStyles();
  return (
    <Accordion>
      <AccordionSummary
        expandIcon={<ExpandMore />}
        aria-controls="panel1bh-content"
        id="panel1bh-header"
      >
        <Typography className={classes.heading}>
          Walmart Real Estate FM Decision Engine
        </Typography>
      </AccordionSummary>
      <AccordionDetails>
        <ul>
          <li>
            <Typography>
              Designed and developed UI / API to allow users to make informed
              decisions on proposals
            </Typography>
          </li>
          <li>
            <Typography>
              Designed and developed SPA Frontend UI using Angular-7 and Angular
              Material
              <ul>
                <li>
                  Implemented <i>NGX-Charts</i> framework for graphs
                </li>
                <li>
                  Implemented <i>NGX-Print</i> to screenshot current page (print
                  or save to pdf)
                </li>
                <li>
                  Developed User/Product metrics/loggers for Application
                  usage/monitor/graphs
                </li>
                <li>Developed Users / Business Rules Management Tools</li>
              </ul>
            </Typography>
          </li>
          <li>
            <Typography>
              Designed and developed web services API using Spring Boot + JPA
              <ul>
                <li>Mapped entity model to table in Data Warehouse</li>
                <li>
                  Implemented services to allow users to make decision(s) to
                  proposals
                </li>
                <li>Implemented Web Socket Service to allow live data feed</li>
                <li>Integration with Service Channel API</li>
                <li>Developed scheduled functions to calculate Savings</li>
                <li>
                  Developed live notification application to let user(s) know if
                  any dependent server / API / ETL workflow failed or are down
                </li>
              </ul>
            </Typography>
          </li>
          <li>
            <Typography>
              Integrated with Alteryx workflow for live data feed to users
            </Typography>
          </li>
          <li>
            <Typography>
              Implemented SSO using Walmart LDAP server coupled with Spring
              Security and JWT to achieve stateless sessions
            </Typography>
          </li>
          <li>
            <Typography>
              Build / Deploy via Walmart Looper(Jenkins) / OneOps Cloud
            </Typography>
          </li>
        </ul>
      </AccordionDetails>
    </Accordion>
  );
};

export default Y2019;
