import React from "react";
import {
  Typography,
  Accordion,
  AccordionDetails,
  AccordionSummary,
} from "@material-ui/core";
import { ExpandMore } from "@material-ui/icons";
// Styles
import { useStyles } from "./ItemStyles";

const Y2017 = () => {
  const classes = useStyles();
  return (
    <Accordion>
      <AccordionSummary
        expandIcon={<ExpandMore />}
        aria-controls="panel1bh-content"
        id="panel1bh-header"
      >
        <Typography className={classes.heading}>DataStage Jobs</Typography>
      </AccordionSummary>
      <AccordionDetails>
        <ul>
          <li>
            <Typography>
              Assisted in development of DataStage jobs in ETL process
            </Typography>
          </li>
          <li>
            <Typography>
              Transform data based on client needs following data naming
              conventions
              <ul>
                <li>Developed sequence jobs to run all parallel jobs</li>
              </ul>
            </Typography>
          </li>
          <li>
            <Typography>
              Load data back into tables in testing server
            </Typography>
          </li>
          <li>
            <Typography>Test and documented project job files</Typography>
          </li>
          <li>
            <Typography>
              Exported project into production server after testing are
              completed
            </Typography>
          </li>
        </ul>
      </AccordionDetails>
    </Accordion>
  );
};

export default Y2017;
